/* global $ */
import React from 'react';
import SLDSSection from './SLDSSection';
import GRAXObjectCombobox from './GRAXObjectCombobox';
import GRAXTree from './GRAXTree';
import GRAXDataTable from './GRAXDataTable';

import {Input, InputIcon, Button} from '@salesforce/design-system-react';

import {getCookie, setCookie} from '../lib/utils';


export default class GRAXRestore extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            selectedDataIndex: null,
            retrieved: false
        }
    }

    getSelectedFields() {
        return getCookie(this.state.selectedDataIndex.id + 'Fields').split(',');
    }

    render() {
        return (
            <div>
                <SLDSSection title="Select the GRAX Table">
                    <GRAXObjectCombobox
                        onSelect={(event, data) => {
                            this.setState({
                                selectedDataIndex: data[0]
                            });
                        }}
                        onRequestRemoveSelectedOption={(event, data) => {
                            this.setState({
                                selectedDataIndex: null
                            });
                        }}
                    />
                </SLDSSection>
                
                <SLDSSection title="Search GRAX Table" className="slds-p-top_large">
                    {
                        this.state.selectedDataIndex != null
                        ? <GRAXTree dataIndex={this.state.selectedDataIndex.id} />
                        : 'No GRAX Table selected...'
                    }
                </SLDSSection>

                <SLDSSection title="Filter Records" className="slds-p-top_large">
                    <Input
                        iconLeft={
                            <InputIcon
                                assistiveText="Search"
                                name="search"
                                category="utility"
                            />
                        }
                        assistiveText={{ label: 'Search Term' }}
                        id="record-query"
                        placeholder="Search"
                    />
                </SLDSSection>

                <SLDSSection title="GRAX Stored Records" className="slds-p-top_large">
                    <div className="-x-small-buttons--horizontal slds-p-bottom_large">
					    <Button label="Retrieve" variant="brand" onClick={() => {
                            this.setState({
                                retrieved: this.state.selectedDataIndex != null
                            })
                        }}/>
                    </div>

                    {this.state.retrieved && <GRAXDataTable columns={this.getSelectedFields()} dataIndex={this.state.selectedDataIndex.id} />}
                </SLDSSection>
            </div>
        );
    }
}