import React from 'react';
import $ from 'jquery';
import '../public/js/jquery.sldsTree.js';

import {baseUrl, GetParams} from '../lib/grax-data-lake';

import {getCookie, setCookie} from '../lib/utils';

import _ from 'lodash';


var selectedDataIndex;

var renderFieldListSelector = function($fieldListSelector, $fieldlist, data) {
    data.sort(function (a, b) {
        if (a.label > b.label) {
            return 1;
        }

        if (a.label < b.label) {
            return -1;
        }
        
        return 0;
    });

    $fieldListSelector.sldsTree({ 
        data: data,
        attrs: {
            root: {'class': 'slds-scrollable_y', 'style': 'max-height: 25vh'}
        },
        onDiv: function($div, item, settings) {
            $div.append('<div class="slds-size_1-of-1">\
            <div class="slds-form-element">\
                <div class="slds-form-element__control">\
                    <span class="slds-checkbox">\
                    <input name="field_' + item[settings.items.id] + '" id="field_' + item[settings.items.id] + '" type="checkbox" ' + (item.isSelected ? 'checked' : '') + ' />\
                    <label class="slds-checkbox__label" for="field_' + item[settings.items.id] + '">\
                        <span class="slds-checkbox_faux"></span>\
                        <span class="slds-form-element__label">' + item[settings.items.label] + '</span>\
                    </label>\
                    </span>\
                </div>\
                </div>\
            </div>');
        },
        onFinish: function($root, items, settings) {
            var fields = {};
            var countChecked = 1;

            var getSelectedFields = function() {
                var selectedFields = [];

                $.each(fields, function(field, selected) {
                    if(selected) {
                        selectedFields.push(field);
                    }
                });

                return selectedFields;
            };

            var cookieSelected = getCookie(selectedDataIndex + 'Fields');

            var updateValue = function() {
                var selectedFields = getSelectedFields().join(',');

                $fieldlist.val(selectedFields);
                setCookie(selectedDataIndex + 'Fields', selectedFields);
            };

            $.each(items, function(i, item) {
                var id = item[settings.items.id];

                if(cookieSelected) {
                    fields[id] = cookieSelected.split(',').indexOf(id) != -1;
                } else {
                    fields[id] = item.isSelected;
                }

                $('#field_' + id).on('change', function(e) {
                    if(e.target.checked && countChecked >= 9) {
                        e.target.checked = false;
                        e.preventDefault();
                        
                        return false;
                    }

                    fields[id] = e.target.checked;

                    if(e.target.checked) {
                        countChecked++;
                    } else {
                        countChecked--;
                    }

                    updateValue();
                }).prop('checked', fields[id]);

                updateValue();
            });
        }
    });
};

export default class GRAXTree extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            items: [],
            isLoaded: false
        };
    }

    componentDidMount() {
        this.$el = $(this.el);
        this.$fieldlist = $(this.fieldList);

        selectedDataIndex = this.props.dataIndex;

        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/getDataTypeFields/' + selectedDataIndex + GetParams({action:'load'}),
            data: {},
            success: function(data) {
                this.setState({
                    items: _.map(data, (val, key) => ({
                        id: key,
                        label: key
                    })),
                    isLoaded: true
                });

                renderFieldListSelector(this.$el, this.$fieldlist, this.state.items);
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    isLoaded: true,
                    error: err.toString()
                });
            }.bind(this)
        });
    }

    render() {
        return (
            <div>
                <input type="hidden" ref={fieldlist => this.fieldlist = fieldlist} />
                <div ref={el => this.el = el} />
            </div>
        );
    }
}