import React from 'react';
import $ from 'jquery';

import {Button, Dropdown, DataTable, DataTableColumn, DataTableCell, DataTableRowActions, Checkbox} from '@salesforce/design-system-react';

import {baseUrl, GetParams} from '../lib/grax-data-lake';

import _ from 'lodash';
import moment from 'moment';


export default class GRAXDataTable extends React.Component {
	constructor(props) {
        super(props);

        this.state = {
			sortColumn: 'Id',
			sortColumnDirection: {
				Id: 'asc',
			},
            chunks: [],
            items: [],
            page: 0,
            selection: [],
            isLoaded: false,
            _sfRequestsDisabled: false,
            _restoreChildrens: true
		};
    }
    
    componentDidMount() {
        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake' + GetParams({
                action:'load',
                dataIndex: this.props.dataIndex,
                _select: this.props.columns.join(',') || 'Id'
            }),
            /*data: {
                _select: this.props.columns.join(',')
            },*/
            success: function(data) {
                var chunks = _.chunk(_.map(data, (item, i) => {
                    _.forEach(item, (val, key) => {
                        if(typeof(val) == 'string') {
                            var m = moment(val);

                            if(m.isValid()) {
                                item[key] = m.format(m._i.indexOf(':') != -1 ? 'MM/DD/YYYY hh:mm a' : 'MM/DD/YYYY');
                            }
                        }
                    });

                    return {
                        ...item,
                        ...{id: i.toString()}
                    }
                }), 25);

                this.setState({
                    page: 0,
                    data: data,
                    chunks: chunks,
                    items: chunks[0],
                    isLoaded: true
                });
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    isLoaded: true,
                    chunks: [],
                    items: [],
                    error: err.toString()
                });
            }.bind(this)
        });
    }

	handleChanged(event, data) {
		this.setState({ selection: data.selection });
		console.log(event, data);
	}

	handleSort(sortColumn, ...rest) {
		/*if (this.props.log) {
			this.props.log('sort')(sortColumn, ...rest);
		}*/

		const sortProperty = sortColumn.property;
		const sortDirection = sortColumn.sortDirection;
		const newState = {
			sortColumn: sortProperty,
			sortColumnDirection: {
				[sortProperty]: sortDirection,
			},
			items: [...this.state.items],
		};

		// needs to work in both directions
		newState.items = newState.items.sort((a, b) => {
			let val = 0;

			if (a[sortProperty] > b[sortProperty]) {
				val = 1;
			}
			if (a[sortProperty] < b[sortProperty]) {
				val = -1;
			}

			if (sortDirection === 'desc') {
				val *= -1;
			}

			return val;
		});

		this.setState(newState);
	}

	render() {
		return (
			<div>
                <DataTable
                    assistiveText={{
                        actionsHeader: 'actions',
                        columnSort: 'sort this column',
                        columnSortedAscending: 'asc',
                        columnSortedDescending: 'desc',
                        selectAllRows: 'all rows',
                        selectRow: 'select this row',
                    }}
                    fixedLayout
                    items={this.state.items}
                    id="DataTableExample-2"
                    onRowChange={this.handleChanged.bind(this)}
                    onSort={this.handleSort.bind(this)}
                    selection={this.state.selection}
                    selectRows
                >

                    {this.props.columns.map((columnName) => (
                        <DataTableColumn
                            key={'col' + columnName}
                            id={columnName}
                            isSorted={this.state.sortColumn === columnName}
                            label={columnName} // TODO
                            property={columnName}
                            sortable
                            sortDirection={this.state.sortColumnDirection[columnName]}
                        />
                    ))}
                </DataTable>

                {Array.apply(0, Array(this.state.chunks.length)).map((x, i) => {
                    return <Button
                        label={i}
                        key={i}
                        onClick={() => {
                            this.setState({
                                page: i,
                                items: this.state.chunks[i]
                            });
                        }}
                        variant="base"
                    />
                })}

                <div className="-x-small-buttons--horizontal slds-p-top_small">
                    <div>
                        <Checkbox
                            defaultChecked={this.state._restoreChildrens}
                            onChange={(isChecked) => {
                                this.setState({
                                    _restoreChildrens: isChecked
                                });
                            }}
                            label="Restore Childrens?"
                        />
                    </div>

                    <div>
                        <Checkbox
                            defaultChecked={this.state._sfRequestsDisabled}
                            onChange={(isChecked) => {
                               this.setState({
                                    _sfRequestsDisabled: isChecked
                               });
                            }}
                            label="Skip Salesforce Queries?"
                        />
                    </div>

                    <Button label="Restore Selection" disabled={this.state.selection.length < 1} onClick={() => {
                        if(confirm('Are you sure that you want to re-create the selected records?')) {
                            $.ajax({
                                type: 'POST',
                                url: baseUrl + '/grax-app/grax-data-lake' + GetParams({
                                    action: 'restore',
                                    restoreChildrens: this.state._restoreChildrens,
                                    sfRequestsDisabled: this.state._sfRequestsDisabled
                                }),
                                contentType: "application/json; charset=utf-8",
                                data: JSON.stringify(_.map(this.state.selection, function(item) {
                                    return _.omit(item, ['id']);
                                })),
                                success: function(data) {
                                    alert(data.state);
                                }.bind(this)
                            });

                            this.setState({
                                selection: []
                            });
                        }
                    }} />
                </div>
			</div>
		);
	}
}