import React from 'react';
import ReactDOM from 'react-dom';
import { Tabs, TabsPanel, IconSettings } from '@salesforce/design-system-react';

//import '@salesforce-ux/design-system/assets/styles/salesforce-lightning-design-system.min.css';
import './assets/styles/salesforce-lightning-design-system.min.css';

import GRAXRestore from './components/GRAXRestore';
import GRAXRestoreMapping from './components/GRAXRestoreMapping';

class GRAXApp extends React.Component {
    render() {
        return (
            <IconSettings iconPath="./assets/icons">
                <Tabs variant="scoped" id="grax-tabs">
                    <TabsPanel label="Restore">
                        <GRAXRestore />
                    </TabsPanel>
                    <TabsPanel label="Restore Mapping">
                        <GRAXRestoreMapping />
                    </TabsPanel>
                </Tabs>
            </IconSettings>
        )
    }
}

var mountNode = document.getElementById("app");
ReactDOM.render(<GRAXApp />, mountNode);
